body,
html {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  /* color:rgb(255, 196, 112); */
  /* background-color: rgb(71, 147, 175); */
}

body {
  font-family: "Poppins", sans-serif;
}
p {
  color: rgb(255, 196, 112);
}
/* Header */
.header {
  width: 100%;
  min-height: 50px;
  padding-block: 1rem;
  margin-inline: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(221, 87, 70);
  

}

.header_title {
  width: 300px;
  color: rgb(255, 196, 112);
  font-family: "Dancing Script", cursive;
}

.header__menu {
  font-size: 30px;
  margin: 10px auto;
  display: block;
  width: 45px;
  padding: 5px;
  border: none;
  cursor: pointer;
}

.nav-list ul {
  padding-left: 0;
  display: flex;
  justify-content: center;
  gap: 2rem 1rem;
}

.nav-list ul li {
  list-style-type: none;
}

.nav-list ul li a {
  text-decoration: none;
  font-size: 18px;
  /* color:rgb(254, 251, 246); */
  padding: 15px;
}

.nav-list ul li a:hover {
  text-decoration: underline;
  /* color: rgb(255, 196, 112); */
}

main {
  width: 100%;
  margin: 0 auto;
}
.content {
  padding: 30px;
}

.katalog {
  width: 100%;
  margin: 50px auto;
  text-align: center;
}
.katalog_label {
  font-size: 60px;
  font-weight: lighter;
  color: rgb(63, 29, 56);
  border-bottom: 1px solid rgb(63, 29, 56);
  text-align: center;
  color: rgb(255, 196, 112);
  font-family: "Dancing Script", cursive;
  font-weight: bold;
}

/* posts */
.posts {
  margin: 30px auto auto;
  text-align: left;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-row-gap: 30px;
  grid-column-gap: 16px;
  justify-items: center;
}

/* post item */
.restaurant-item {
  display: flex; /* Menggunakan flexbox */
  flex-direction: column; /* Mengatur tata letak vertikal */
  box-shadow: 0 4px 8px 0 rgb(5, 5, 5);
  border-radius: 5px;
  overflow: hidden;
  max-width: 600px;
  margin: 0 auto; /* Pusatkan div di tengah halaman */
  margin-bottom: 20px;
  transition: background-color 0.3s ease; /* Efek transisi warna latar belakang */
}

.restaurant-item:hover {
  background-color: rgb(255, 196, 112); /* Warna latar belakang saat dihover */
}

.item_thumbnail {
  width: 100%; /* Lebar gambar mengisi kontainer */
  height: auto; /* Tinggi gambar menyesuaikan proporsi */
}

.item_content {
  padding: 10px; /* Ruang di dalam kontainer */
}


.item_content {
  padding: 16px;
  /* color: rgb(63, 29, 56); */
}

.item_content:hover {
  background-color: rgba(
    63,
    29,
    56,
    0.1
  ); /* Warna latar belakang saat kursor menyentuh */
}

.item_thumbnail {
  width: 100%;
  aspect-ratio: 16 / 9;
  object-fit: cover;
  transition: transform 0.3s ease; /* Efek transisi */
}

.item_thumbnail:hover {
  transform: scale(1.1); /* Zoom in saat dihover */
}

.item_rating {
  font-size: 14px;
  font-weight: bold;
  color: rgb(162, 103, 138);
}

.item_title {
  font-weight: bold;
  font-size: 20px;
  margin-top: 16px;
  /* color: rgb(63, 29, 56); */
}

.restaurant-item_title a {
  text-decoration: none;
  /* color: rgb(63, 29, 56); */
}

.restaurant-item_title a:hover {
  /* color:  rgb(63, 29, 56); */
  cursor: pointer;
}

.item_description {
  margin-top: 16px;
  font-size: 12px;
  line-height: 1.5em;
  width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.item_city {
  margin-top: 16px;
  font-size: 14px;
  font-weight: bold;
  color: rgb(63, 29, 56);
}



/* skip-link */
.skip-link {
  position: absolute;
  top: -50px;
  left: 0;
  padding: 12px;
  /* background-color: #bf1722;
  color: rgb(254, 251, 246); */
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

/* detail */
/* .resto-detail {
  max-width: 800px;
} */

.container {
  margin-top: 2rem;
  /* color: rgb(63, 29, 56); */
  /* box-shadow: 0 4px 8px 0 black; */
  width: 100%;
  /* background-color:rgb(254, 251, 246); */
}

.resto-info_name {
  font-size: 30px;
  margin-bottom: 20px;
}

.resto-info_img {
  width: 100%;
  aspect-ratio: 16 / 9;
  object-fit: cover;
  max-width: 700px;
  transition: transform 0.3s ease; /* Efek transisi */
}

.resto-info_img:hover {
  transform: scale(1.1); /* Zoom in saat dihover */
}

.resto-info_city {
  margin-top: 16px;
  font-size: 20px;
  font-weight: bold;
  text-align: left;
  text-align: center;
  color: rgb(63, 29, 56);
}

.info {
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 15px;
}

.resto-info_description {
  margin-top: 16px;

  text-align: left;
}

.resto-info_address {
  margin-top: 16px;
  text-align: left;
  font-weight: bold;
  text-align: right;
  color: rgb(63, 29, 56);
}

.menu_title {
  margin-top: 20px;
  font-weight: bold;
  font-size: 30px;
  /* border-bottom: 1px solid rgb(63, 29, 56); */
  max-width: 700px;
  display: inline-block;
  width: 100%;
  /* color:rgb(221, 87, 70); */
  font-family: "Dancing Script", cursive;
}

/* Tata letak menu */
.resto-info_menu {
  display: flex;

  margin-bottom: 20px;
  padding: 5px;
}

/* Gaya untuk setiap kategori menu */
.menu_category {
  font-weight: bold;
  font-size: 25px;
  margin-bottom: 10px;
  /* color:rgb(255, 196, 112);  */
  margin-top: 10px;
  padding: 10px;
  /* background-color:rgb(139, 50, 44);
  border-bottom: 1px solid rgb(63, 29, 56); */
  font-family: "Dancing Script", cursive;
}

/* Gaya daftar menu */
.category_list {
  list-style-type: none;
  padding: 10;
}

/* Gaya untuk setiap item menu */
.menu_category_list li {
  margin-bottom: 8px;
  font-size: 16px;
  /* color: 666666; Warna teks */
}

.reviews_title {
  font-weight: bold;
  font-size: 35px;
  border-bottom: 2px solid rgb(63, 29, 56);
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
  color: rgb(71, 147, 175);
  font-family: "Dancing Script", cursive;
}

.reviews {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-row-gap: 30px;
  grid-column-gap: 16px;
  justify-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition:
    box-shadow 0.3s ease,
    filter 0.5s ease;
}
.resto_reviews:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
  filter: brightness(110%);
}

.resto_reviews:active {
  transform: translateY(0);
  filter: brightness(110%);
}

.resto_reviews {
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */
  width: 100%;
  border-radius: 5px;
  padding: 15px;
  /* background-color:rgb(139, 50, 44);
  color:rgb(255, 196, 112); */
}

.menuku {
  width: 100%;
  border-radius: 5px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background-color: rgb(139, 50, 44);
  color: rgb(255, 196, 112);
}
.customer {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 8px;
}
.grid-card {
  width: 800px; /* Atur lebar card sesuai kebutuhan */
  margin: auto;
  padding: 25px 25px 25px 25px;
  border-radius: 8px;
  background-color: #f0f0f0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 30px;
  margin-top: 30px;
  background-color: rgb(139, 50, 44);
  color: rgb(255, 196, 112);
}

.grid-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
  filter: brightness(150%);
}

.grid-card img {
  width: 30%;
  border-radius: 8px;
  margin-bottom: 10px;
}

.grid-card h2 {
  margin-top: 0;
}

.grid-card p {
  margin-bottom: 0;
}
.review_date {
  margin-top: 5px;
  font-size: 14px;
}


.reviewSection {
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

.reviewItem {
  width: 300px;
  padding: 10px;
  margin: 1rem;
  cursor: pointer;
  border-radius: 10px;
  background-color: #10102a;
  border: 1px solid #10102a;
  transition: all 0.2s linear;
}

.reviewItem:hover {
  border-color: aqua;
  transform: scale(1.01);
  background-color: #090921;
  box-shadow: 0 0px 5px 0px #cbc0c0;
}

.top {
  margin-bottom: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.top ul {
  display: flex;
  list-style: none;
}

.top ul li {
  padding-left: 4px;
}
.clientImage {
  margin-bottom: 0.6rem;
}

article p {
  font-size: 15px;
  font-weight: 100;
  margin-bottom: 1rem;
  font-family: system-ui;
}

.flex-container {
  display: flex;
  margin-bottom: 20px; /* Jarak antara setiap item */
}

.image-container {
  flex: 0 0 auto; /* Lebar tetap untuk kontainer gambar */
  margin-right: 20px; /* Jarak antara gambar dan deskripsi */
}

.description-container {
  flex: 1; /* Kontainer deskripsi akan mengambil sisa ruang */
}

.description-container h2 {
  margin-top: 0;
}

.description-container p {
  margin-bottom: 0;
}

.interactive-link {
  display: inline-block;
  padding: 10px;
  min-width: 44px;
  min-height: 44px;
  text-decoration: none;
  transition: transform 0.2s;
  color: rgb(63, 29, 56);
}

.interactive-link:hover {
  transform: scale(1.1);
}
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

.loading-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.spinner {
  animation: rotate 1s linear infinite;
  width: 50px;
  height: 50px;
}
.path {
  stroke: #4285f4;
  stroke-linecap: round;
  animation: dash 1.5s ease-in-out infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}



/* Gaya untuk input teks */
#query {
  width: 100%; /* Lebar input 100% dari div parent */
  padding: 10px; /* Berikan ruang tambahan di dalam input */
  margin-bottom: 15px; /* Berikan ruang antara input dan elemen-elemen berikutnya */
  box-sizing: border-box; /* Pastikan padding tidak mempengaruhi lebar total */
  border: 1px solid #ccc; /* Berikan garis pinggir */
  border-radius: 5px; /* Atur sudut lengkung */
}

/* Gaya untuk daftar restoran */
.hero {
  width: 100%;
  box-sizing: border-box;
}

.hero img {
  width: 100%;
  height: auto;
  display: block;
}
