;EACnB,aAAa;EACb,mBAAmB;EACnB,mBAAmB;EACnB,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;AACnB;AACA;EACE,qBAAqB;AACvB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,mBAAmB;EACnB,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,mBAAmB,EAAE,6BAA6B;AACpD;;AAEA;EACE,cAAc,EAAE,uCAAuC;EACvD,kBAAkB,EAAE,sCAAsC;AAC5D;;AAEA;EACE,OAAO,EAAE,kDAAkD;AAC7D;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,eAAe;EACf,gBAAgB;EAChB,qBAAqB;EACrB,0BAA0B;EAC1B,sBAAsB;AACxB;;AAEA;EACE,qBAAqB;AACvB;AACA;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,yBAAyB;;EAEzB,eAAe;EACf,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,SAAS;;EAET,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,sBAAsB;AACxB;AACA;EACE,oCAAoC;EACpC,WAAW;EACX,YAAY;AACd;AACA;EACE,eAAe;EACf,qBAAqB;EACrB,yCAAyC;AAC3C;;AAEA;EACE;IACE,yBAAyB;EAC3B;AACF;;AAEA;EACE;IACE,wBAAwB;IACxB,oBAAoB;EACtB;EACA;IACE,yBAAyB;IACzB,sBAAsB;EACxB;EACA;IACE,yBAAyB;IACzB,uBAAuB;EACzB;AACF;;;;AAIA,0BAA0B;AAC1B;EACE,WAAW,EAAE,qCAAqC;EAClD,aAAa,EAAE,0CAA0C;EACzD,mBAAmB,EAAE,4DAA4D;EACjF,sBAAsB,EAAE,oDAAoD;EAC5E,sBAAsB,EAAE,0BAA0B;EAClD,kBAAkB,EAAE,wBAAwB;AAC9C;;AAEA,+BAA+B;AAC/B;EACE,WAAW;EACX,sBAAsB;AACxB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,cAAc;AAChB","sourcesContent":["* {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\nbody,\nhtml {\n  width: 100%;\n  height: 100%;\n  margin: 0px;\n  padding: 0px;\n  overflow-x: hidden;\n  /* color:rgb(255, 196, 112); */\n  /* background-color: rgb(71, 147, 175); */\n}\n\nbody {\n  font-family: \"Poppins\", sans-serif;\n}\np {\n  color: rgb(255, 196, 112);\n}\n/* Header */\n.header {\n  width: 100%;\n  min-height: 50px;\n  padding-block: 1rem;\n  margin-inline: auto;\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  background-color: rgb(221, 87, 70);\n  \n\n}\n\n.header_title {\n  width: 300px;\n  color: rgb(255, 196, 112);\n  font-family: \"Dancing Script\", cursive;\n}\n\n.header__menu {\n  font-size: 30px;\n  margin: 10px auto;\n  display: block;\n  width: 45px;\n  padding: 5px;\n  border: none;\n  cursor: pointer;\n}\n\n.nav-list ul {\n  padding-left: 0;\n  display: flex;\n  justify-content: center;\n  gap: 2rem 1rem;\n}\n\n.nav-list ul li {\n  list-style-type: none;\n}\n\n.nav-list ul li a {\n  text-decoration: none;\n  font-size: 18px;\n  /* color:rgb(254, 251, 246); */\n  padding: 15px;\n}\n\n.nav-list ul li a:hover {\n  text-decoration: underline;\n  /* color: rgb(255, 196, 112); */\n}\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n.content {\n  padding: 30px;\n}\n\n.katalog {\n  width: 100%;\n  margin: 50px auto;\n  text-align: center;\n}\n.katalog_label {\n  font-size: 60px;\n  font-weight: lighter;\n  color: rgb(63, 29, 56);\n  border-bottom: 1px solid rgb(63, 29, 56);\n  text-align: center;\n  color: rgb(255, 196, 112);\n  font-family: \"Dancing Script\", cursive;\n  font-weight: bold;\n}\n\n/* posts */\n.posts {\n  margin: 30px auto auto;\n  text-align: left;\n\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n  grid-row-gap: 30px;\n  grid-column-gap: 16px;\n  justify-items: center;\n}\n\n/* post item */\n.restaurant-item {\n  display: flex; /* Menggunakan flexbox */\n  flex-direction: column; /* Mengatur tata letak vertikal */\n  box-shadow: 0 4px 8px 0 rgb(5, 5, 5);\n  border-radius: 5px;\n  overflow: hidden;\n  max-width: 600px;\n  margin: 0 auto; /* Pusatkan div di tengah halaman */\n  margin-bottom: 20px;\n  transition: background-color 0.3s ease; /* Efek transisi warna latar belakang */\n}\n\n.restaurant-item:hover {\n  background-color: rgb(255, 196, 112); /* Warna latar belakang saat dihover */\n}\n\n.item_thumbnail {\n  width: 100%; /* Lebar gambar mengisi kontainer */\n  height: auto; /* Tinggi gambar menyesuaikan proporsi */\n}\n\n.item_content {\n  padding: 10px; /* Ruang di dalam kontainer */\n}\n\n\n.item_content {\n  padding: 16px;\n  /* color: rgb(63, 29, 56); */\n}\n\n.item_content:hover {\n  background-color: rgba(\n    63,\n    29,\n    56,\n    0.1\n  ); /* Warna latar belakang saat kursor menyentuh */\n}\n\n.item_thumbnail {\n  width: 100%;\n  aspect-ratio: 16 / 9;\n  object-fit: cover;\n  transition: transform 0.3s ease; /* Efek transisi */\n}\n\n.item_thumbnail:hover {\n  transform: scale(1.1); /* Zoom in saat dihover */\n}\n\n.item_rating {\n  font-size: 14px;\n  font-weight: bold;\n  color: rgb(162, 103, 138);\n}\n\n.item_title {\n  font-weight: bold;\n  font-size: 20px;\n  margin-top: 16px;\n  /* color: rgb(63, 29, 56); */\n}\n\n.restaurant-item_title a {\n  text-decoration: none;\n  /* color: rgb(63, 29, 56); */\n}\n\n.restaurant-item_title a:hover {\n  /* color:  rgb(63, 29, 56); */\n  cursor: pointer;\n}\n\n.item_description {\n  margin-top: 16px;\n  font-size: 12px;\n  line-height: 1.5em;\n  width: 100%;\n  display: -webkit-box;\n  -webkit-line-clamp: 3;\n  -webkit-box-orient: vertical;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.item_city {\n  margin-top: 16px;\n  font-size: 14px;\n  font-weight: bold;\n  color: rgb(63, 29, 56);\n}\n\n\n\n/* skip-link */\n.skip-link {\n  position: absolute;\n  top: -50px;\n  left: 0;\n  padding: 12px;\n  /* background-color: #bf1722;\n  color: rgb(254, 251, 246); */\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n/* detail */\n/* .resto-detail {\n  max-width: 800px;\n} */\n\n.container {\n  margin-top: 2rem;\n  /* color: rgb(63, 29, 56); */\n  /* box-shadow: 0 4px 8px 0 black; */\n  width: 100%;\n  /* background-color:rgb(254, 251, 246); */\n}\n\n.resto-info_name {\n  font-size: 30px;\n  margin-bottom: 20px;\n}\n\n.resto-info_img {\n  width: 100%;\n  aspect-ratio: 16 / 9;\n  object-fit: cover;\n  max-width: 700px;\n  transition: transform 0.3s ease; /* Efek transisi */\n}\n\n.resto-info_img:hover {\n  transform: scale(1.1); /* Zoom in saat dihover */\n}\n\n.resto-info_city {\n  margin-top: 16px;\n  font-size: 20px;\n  font-weight: bold;\n  text-align: left;\n  text-align: center;\n  color: rgb(63, 29, 56);\n}\n\n.info {\n  padding-left: 30px;\n  padding-right: 30px;\n  padding-bottom: 15px;\n}\n\n.resto-info_description {\n  margin-top: 16px;\n\n  text-align: left;\n}\n\n.resto-info_address {\n  margin-top: 16px;\n  text-align: left;\n  font-weight: bold;\n  text-align: right;\n  color: rgb(63, 29, 56);\n}\n\n.menu_title {\n  margin-top: 20px;\n  font-weight: bold;\n  font-size: 30px;\n  /* border-bottom: 1px solid rgb(63, 29, 56); */\n  max-width: 700px;\n  display: inline-block;\n  width: 100%;\n  /* color:rgb(221, 87, 70); */\n  font-family: \"Dancing Script\", cursive;\n}\n\n/* Tata letak menu */\n.resto-info_menu {\n  display: flex;\n\n  margin-bottom: 20px;\n  padding: 5px;\n}\n\n/* Gaya untuk setiap kategori menu */\n.menu_category {\n  font-weight: bold;\n  font-size: 25px;\n  margin-bottom: 10px;\n  /* color:rgb(255, 196, 112);  */\n  margin-top: 10px;\n  padding: 10px;\n  /* background-color:rgb(139, 50, 44);\n  border-bottom: 1px solid rgb(63, 29, 56); */\n  font-family: \"Dancing Script\", cursive;\n}\n\n/* Gaya daftar menu */\n.category_list {\n  list-style-type: none;\n  padding: 10;\n}\n\n/* Gaya untuk setiap item menu */\n.menu_category_list li {\n  margin-bottom: 8px;\n  font-size: 16px;\n  /* color: 666666; Warna teks */\n}\n\n.reviews_title {\n  font-weight: bold;\n  font-size: 35px;\n  border-bottom: 2px solid rgb(63, 29, 56);\n  width: 100%;\n  max-width: 300px;\n  margin-bottom: 20px;\n  color: rgb(71, 147, 175);\n  font-family: \"Dancing Script\", cursive;\n}\n\n.reviews {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n  grid-row-gap: 30px;\n  grid-column-gap: 16px;\n  justify-items: center;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition:\n    box-shadow 0.3s ease,\n    filter 0.5s ease;\n}\n.resto_reviews:hover {\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\n  transform: translateY(-5px);\n  filter: brightness(110%);\n}\n\n.resto_reviews:active {\n  transform: translateY(0);\n  filter: brightness(110%);\n}\n\n.resto_reviews {\n  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */\n  width: 100%;\n  border-radius: 5px;\n  padding: 15px;\n  /* background-color:rgb(139, 50, 44);\n  color:rgb(255, 196, 112); */\n}\n\n.menuku {\n  width: 100%;\n  border-radius: 5px;\n  padding: 10px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n\n  background-color: rgb(139, 50, 44);\n  color: rgb(255, 196, 112);\n}\n.customer {\n  font-weight: bold;\n  font-size: 18px;\n  margin-bottom: 8px;\n}\n.grid-card {\n  width: 800px; /* Atur lebar card sesuai kebutuhan */\n  margin: auto;\n  padding: 25px 25px 25px 25px;\n  border-radius: 8px;\n  background-color: #f0f0f0;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  text-align: center;\n  margin-bottom: 30px;\n  margin-top: 30px;\n  background-color: rgb(139, 50, 44);\n  color: rgb(255, 196, 112);\n}\n\n.grid-card:hover {\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\n  transform: translateY(-5px);\n  filter: brightness(150%);\n}\n\n.grid-card img {\n  width: 30%;\n  border-radius: 8px;\n  margin-bottom: 10px;\n}\n\n.grid-card h2 {\n  margin-top: 0;\n}\n\n.grid-card p {\n  margin-bottom: 0;\n}\n.review_date {\n  margin-top: 5px;\n  font-size: 14px;\n}\n\n\n.reviewSection {\n  padding: 1rem;\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n  justify-content: space-around;\n}\n\n.reviewItem {\n  width: 300px;\n  padding: 10px;\n  margin: 1rem;\n  cursor: pointer;\n  border-radius: 10px;\n  background-color: #10102a;\n  border: 1px solid #10102a;\n  transition: all 0.2s linear;\n}\n\n.reviewItem:hover {\n  border-color: aqua;\n  transform: scale(1.01);\n  background-color: #090921;\n  box-shadow: 0 0px 5px 0px #cbc0c0;\n}\n\n.top {\n  margin-bottom: 1rem;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.top ul {\n  display: flex;\n  list-style: none;\n}\n\n.top ul li {\n  padding-left: 4px;\n}\n.clientImage {\n  margin-bottom: 0.6rem;\n}\n\narticle p {\n  font-size: 15px;\n  font-weight: 100;\n  margin-bottom: 1rem;\n  font-family: system-ui;\n}\n\n.flex-container {\n  display: flex;\n  margin-bottom: 20px; /* Jarak antara setiap item */\n}\n\n.image-container {\n  flex: 0 0 auto; /* Lebar tetap untuk kontainer gambar */\n  margin-right: 20px; /* Jarak antara gambar dan deskripsi */\n}\n\n.description-container {\n  flex: 1; /* Kontainer deskripsi akan mengambil sisa ruang */\n}\n\n.description-container h2 {\n  margin-top: 0;\n}\n\n.description-container p {\n  margin-bottom: 0;\n}\n\n.interactive-link {\n  display: inline-block;\n  padding: 10px;\n  min-width: 44px;\n  min-height: 44px;\n  text-decoration: none;\n  transition: transform 0.2s;\n  color: rgb(63, 29, 56);\n}\n\n.interactive-link:hover {\n  transform: scale(1.1);\n}\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n.loading-indicator {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n}\n.spinner {\n  animation: rotate 1s linear infinite;\n  width: 50px;\n  height: 50px;\n}\n.path {\n  stroke: #4285f4;\n  stroke-linecap: round;\n  animation: dash 1.5s ease-in-out infinite;\n}\n\n@keyframes rotate {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes dash {\n  0% {\n    stroke-dasharray: 1, 150;\n    stroke-dashoffset: 0;\n  }\n  50% {\n    stroke-dasharray: 90, 150;\n    stroke-dashoffset: -35;\n  }\n  100% {\n    stroke-dasharray: 90, 150;\n    stroke-dashoffset: -124;\n  }\n}\n\n\n\n/* Gaya untuk input teks */\n#query {\n  width: 100%; /* Lebar input 100% dari div parent */\n  padding: 10px; /* Berikan ruang tambahan di dalam input */\n  margin-bottom: 15px; /* Berikan ruang antara input dan elemen-elemen ber