___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 650px) {
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
  }

  .header_title,
  .nav-list {
    padding: 10px;
  }

  .hero_title,
  .hero_tagline {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 759px) {
  .nav-list {
    z-index: 10;
    background-color: black;
    color: rgb(254, 251, 246);
    padding: 0;
    width: 200px;
    position: absolute;
    top: 169px;
    left: 0;

    -webkit-transform: translate(-300px, 0);
    transform: translate(-300px, 0);
  }

  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    transition: transform 0.3s ease;
  }

  .nav-list ul {
    display: flex;
    flex-direction: column;
    gap: 0;
  }

  .nav-list ul li a {
    color: rgb(254, 251, 246);
  }

  .nav_item {
    display: list-item;
    border-bottom: 1px solid rgb(254, 251, 246) e;
    width: 100%;
    text-align: left;
    padding: 10px;
  }
}

@media screen and (max-width: 350px) {
  .posts {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 759px) {
  .header__menu {
    display: block; /* Menampilkan hamburger menu */
  }
}

/* Media query untuk layar dengan lebar minimal 760px */
@media screen and (min-width: 760px) {
  .header__menu {
    display: none; /* Menyembunyikan hamburger menu */
  }
}

@media screen and (min-width: 900px) {
  .posts {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
} 

@media screen and (min-width: 1200px) {
  main {
    max-width: 1200px;
  }

  .hero {
    min-width: 1000px;
  }
}

@media screen and (max-width: 480px) {
  .jumbotron {
    height: 200px;
  }

  .slide h1 {
    font-size: 20px;
    text-align: center;
  }

  .slide p {
    font-size: 12px;
    text-align: center;
  }
}

@media screen and (max-width: 900px) {
  .grid-card {
    width: 70%;
  }

  .grid-card img {
    width: 50%;
  }
}

@media screen and (max-width: 600px) {
  .grid-card {
    width: 90%;
    padding: 15px;
  }

  .grid-card img {
    width: 70%;
  }
}
@media (max-width: 760px) {
  h1 {
    font-size: 24px; 
     text-align: center; 
  }
  
   p {
    font-size: 16px; 
     text-align: center; 
  }
}

@media screen and (max-width: 576px) {
  .reviews_title {
    font-size: 24px;
  }

  .reviews {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-column-gap: 8px;
  }
}

/* Tampilan responsif untuk layar dengan lebar maksimum 360px */
@media screen and (max-width: 360px) {
  .reviews_title {
    font-size: 20px;
  }

  .reviews {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-column-gap: 6px;
  }
}

@media (min-width: 1200px) {
  .hero {
    min-width: 1000px;
  }
}

@media (max-width: 452px) {
  .hero {
    min-width: unset;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,kBAAkB;IAClB,kBAAkB;EACpB;;EAEA;;IAEE,aAAa;EACf;;EAEA;;IAEE,kBAAkB;IAClB,mBAAmB;EACrB;AACF;;AAEA;EACE;IACE,WAAW;IACX,uBAAuB;IACvB,yBAAyB;IACzB,UAAU;IACV,YAAY;IACZ,kBAAkB;IAClB,UAAU;IACV,OAAO;;IAEP,uCAAuC;IACvC,+BAA+B;EACjC;;EAEA;IACE,kCAAkC;IAClC,0BAA0B;IAC1B,+BAA+B;EACjC;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,MAAM;EACR;;EAEA;IACE,yBAAyB;EAC3B;;EAEA;IACE,kBAAkB;IAClB,6CAA6C;IAC7C,WAAW;IACX,gBAAgB;IAChB,aAAa;EACf;AACF;;AAEA;EACE;IACE,0BAA0B;EAC5B;AACF;;AAEA;EACE;IACE,cAAc,EAAE,+BAA+B;EACjD;AACF;;AAEA,uDAAuD;AACvD;EACE;IACE,aAAa,EAAE,kCAAkC;EACnD;AACF;;AAEA;EACE;IACE,aAAa;IACb,kCAAkC;EACpC;AACF;;AAEA;EACE;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;AACF;;AAEA;EACE;IACE,aAAa;EACf;;EAEA;IACE,eAAe;IACf,kBAAkB;EACpB;;EAEA;IACE,eAAe;IACf,kBAAkB;EACpB;AACF;;AAEA;EACE;IACE,UAAU;EACZ;;EAEA;IACE,UAAU;EACZ;AACF;;AAEA;EACE;IACE,UAAU;IACV,aAAa;EACf;;EAEA;IACE,UAAU;EACZ;AACF;AACA;EACE;IACE,eAAe;KACd,kBAAkB;EACrB;;GAEC;IACC,eAAe;KACd,kBAAkB;EACrB;AACF;;AAEA;EACE;IACE,eAAe;EACjB;;EAEA;IACE,4DAA4D;IAC5D,oBAAoB;EACtB;AACF;;AAEA,+DAA+D;AAC/D;EACE;IACE,eAAe;EACjB;;EAEA;IACE,4DAA4D;IAC5D,oBAAoB;EACtB;AACF;;AAEA;EACE;IACE,iBAAiB;EACnB;AACF;;AAEA;EACE;IACE,gBAAgB;EAClB;AACF","sourcesContent":["@media screen and (max-width: 650px) {\n  .header {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    text-align: center;\n    position: relative;\n  }\n\n  .header_title,\n  .nav-list {\n    padding: 10px;\n  }\n\n  .hero_title,\n  .hero_tagline {\n    padding-left: 20px;\n    padding-right: 20px;\n  }\n}\n\n@media screen and (max-width: 759px) {\n  .nav-list {\n    z-index: 10;\n    background-color: black;\n    color: rgb(254, 251, 246);\n    padding: 0;\n    width: 200px;\n    position: absolute;\n    top: 169px;\n    left: 0;\n\n    -webkit-transform: translate(-300px, 0);\n    transform: translate(-300px, 0);\n  }\n\n  .open {\n